@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar .navbar-toggler {
  border-style: none;
  background-color: white;
}

.nav-color {
  background-color: white;
}

.links {
  margin-left: 35px;
  margin-right: 35px;
  font-family: "Roboto", sans-serif;
  font-weight: bolder;
  font-size: larger;
}

.main-logo {
  height: 10rem;
  width: auto;
}

@media only screen and (min-width: 1024px) {
  .main-logo {
    height: 15rem;
    width: auto;
    padding-bottom: 2%;
  }
}

.top-contact {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.phone-number {
  color: #cb9737;
  font-family: "Roboto", sans-serif;
  font-weight: bolder;
  padding-left: 1rem;
  padding-right: 2rem;
  margin-top: 1rem;
}

.small-contact {
  height: 20px;
}

@media only screen and (min-width: 1024px) {
  .phone-number {
    font-size: 25px;
  }
  .small-contact {
    height: 30px;
  }
}

.carousel {
    margin-top: 3%;
    font-family: 'Roboto', sans-serif;
}

@media only screen and (min-width: 1024px) {
    .carousel {
        margin-top: 0.5%;
    }
  }
#example-collapse-text {
  font-family: "Roboto", sans-serif;
  text-align: justify;
  margin: 30px;
  padding: 20px;
}

@media only screen and (min-width: 1024px) {
  #example-collapse-text {
    margin: 30px;
    padding: 20px;
    max-width: 600px;
  }
}

.jumbotron {
  background-color: #007663 !important;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
}

.jumbo-header {
  text-align: center;
  font-weight: bolder;
}

.jumbo-container {
  padding-right: 35px;
  padding-left: 35px;
}

.about-header {
  color: #cb9737;
  font-family: "Roboto", sans-serif;
  font-weight: bolder;
  margin-top: 7%;
  margin-bottom: 7%;
}

.about {
  background-color: #007663;
  font-family: "Roboto", sans-serif;
  text-justify: distribute;
  margin: 30px;
  padding: 25px;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.wojtek {
  height: 150px;
}

.trophy {
  height: 70px;
}

.achievements {
  margin: 30px;
  padding: 20px;
  color: #007663;
}

.treningi-header {
  color: #007663;
  margin-top: 2%;
}

.treningi > div > ul {
  color: #007663;
  text-align: left;
  margin: 15px;
}

.moreinfo {
  padding-bottom: 10%;
}

.contact-info {
  background-color: #007663;
  color: white;
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.contact-details > p {
  text-align: left;
}

.contact-info > h3 {
  padding-top: 2%;
  padding-bottom: 2%;
  font-size: x-large;
}

.icon {
  height: 20px;
  width: auto;
}

.footer {
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0.5rem;
  background-color: #004535;
}

.footer p {
  padding-top: 1.5%;
  font-size: smaller;
  color: white;
}

.footer a {
  text-decoration: none;
  color: white;
}

@media only screen and (min-width: 1024px) {
  .about {
    margin: 50px;
    padding: 50px;
    font-size: 20px;
  }
  .about-header {
    font-size: 40px;
    margin-top: 5%;
    margin-bottom: 5%;
  }
  .trophy {
    height: 100px;
  }
  .achievements {
    font-size: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .treningi {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
  .contact-details p {
    font-size: 20px;
  }
  .contact-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
  .map {
    width: 450px;
    height: 300px;
  }
}

.App {
  text-align: center;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}


